







































@import '~@/variables';

footer {
  background-color: $ops-color-1;
  color: #fff;
  min-height: 400px;
  padding: 2.5rem 1rem;

  a {
    color: #fff;
  }

  ul {
    list-style: none;
    margin: 0;
    padding: 0;
  }
}

.container {
  display: flex;

  &>div {
    margin-right: 2.5rem;

    b {
      display: block;
      text-transform: uppercase;
      margin-bottom: 0.5rem;
    }

    &:last-of-type {
      margin-right: 0;
      flex: 1;
      text-align: right;
    }
  }
}
