@import 'variables';
@import 'helper';

html, body {
  padding: 0;
  margin: 0;
  min-height: 100%;
  position: relative;
  color: $body-font-color;
  font-family: 'Roboto', sans-serif;
  font-size: 16px;
  line-height: 1.2;
  scroll-behavior: smooth;
}

h1 {
  font-size: 2em;
}

h2 {
  font-size: 1.75em;
}

h3 {
  font-size: 1.15em;
}

p, ul, ol {
  line-height: 1.4;
  margin: 0 0 1rem;
}

a {
  text-decoration: none;
}

img {
  display: block;
  max-width: 100%;
}

.container {
  max-width: 1000px!important;
  margin: auto;
}