












































































@import '~@/variables';

@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

main {
  padding: 1rem;
}

section {
  margin-bottom: 2.5rem;
}

#about-us {
  &>div {
    display: flex;
  }

  figure {
    margin: 0;
    margin-left: 1.5rem;
  }

  figcaption {
    text-align: center;
    color: #a8a8a8;
    font-size: 0.8rem;
    margin-top: 0.35rem;
  }

  img {
    border: 1px solid #ccc;
    max-width: 275px;
    border-radius: 275px;
    overflow: hidden;
    animation-name: fadeIn;
    animation-delay: 200ms;
    animation-duration: 900ms;
    animation-fill-mode: forwards;
    animation-timing-function: cubic-bezier(0.33, 1, 0.68, 1);
    opacity: 0;
  }

  .speech-bubble {
    background: #e8e8e8;
    position: relative;
    border-radius: 25px;
    padding: 1.5rem;
    font-style: italic;

    &:after {
      content: "";
      position: absolute;
      top: 30px;
      left: 100%;
      width: 0;
      height: 0;
      border-top: 30px solid transparent;
      border-left: 25px solid #e8e8e8;
      border-bottom: 0 solid transparent;
    }
    P:last-of-type {
      margin-bottom: 0;
    }
  }
}

